.trackorder-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 200px;
  width: 100%;
  text-align: center;
  background: url("../../images/background-img.png");
}

.order-details {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 20px;
  margin-top: 40px;
  margin-bottom: 40px;
}

.order-img img {
  height: 150px;
  width: 150px;
  border-radius: 10px;
}

.order-info {
  flex-grow: 1;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.order-info h2 {
  font-size: 1.5rem;
  color: black;
}

.order-info p {
  margin: 10px 0;
  color: #555;
}

.order-info span {
  font-weight: bold;
  color: #000;
}

.order-actions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.order-actions button i{
    font-size: 1.4rem;
    position: relative;
    top: 3px;
}

.order-actions button {
  padding: 10px 20px;
  border-radius: 10px;
  background-color: #383564;
  color: #fff;
  border: none;
  font-weight: bold;
  cursor: pointer;
  font-size: 1.1rem;
  margin-bottom: 10px;
  white-space: nowrap;
}
.order-actions .cancel-order {
  padding: 10px 20px;
  border-radius: 10px;
  background-color: red;
  color: #fff;
  border: none;
  font-weight: bold;
  cursor: pointer;
  font-size: 1.1rem;
  margin-bottom: 10px;
  white-space: nowrap;
}

.order-actions button:hover{
  background-color: #383564ef;
}

.trackorder-container .order-para{
  margin: 8rem auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 768px) {
  .order-details {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .order-info {
    margin-left: 0;
    margin-top: 15px;
  }

  .order-actions {
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 20px;
  }

  .order-actions button {
    width: 45%;
  }
}

@media screen and (max-width: 576px) {
  .order-actions{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .order-actions button{
    width: 100%;
  }
}
